.App{ 
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;  
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    font-family: 'Helvetica', 'Arial', sans-serif;
}

 /* width */
 ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: rgb(1, 103, 123);;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: white;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(0, 217, 255);
  } 
.sum {
    display:  flex;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    width: 100%;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.logo{
    display: flex;
    align-items: center;
    padding-top: 0.5em;
    margin-left: 1vw;
    margin-right: 1vw;
    color: white;
    font-size: 2em;
    font-weight: bolder;
    height: 2em;
}
.logo-link {
    cursor: pointer;
    padding-top: 0;
    height: 100%;
}
.help{
    margin-right: 2rem;
    color: white;
    height: 100%;
    font-weight: bolder;
}
.ul {
    display: flex;
    margin: 2em;
}
.item{
    display: flex;
}
li{
    margin: 0 1rem;
}
a{
