    }
}
.Room{
    background-color:rgb(1, 103, 123);   
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-template-areas: 
        "header header header header" 
        "users  input  input  input" 
        "chat   video  video  video" 
        "footer footer footer footer";
}
.header{
    grid-area: header; 
    height: 10vh;
}
.videoInput{
    background-color: rgb(26, 25, 25);
    grid-area: input;
}
.video {
    grid-area: video;
    display: flex;
    justify-content: center;
    align-self: center;
    background-color: rgb(26, 25, 25);
    height: 72vh;
}
.chat{
    grid-area: chat;
    background-color: rgb(26, 25, 25);
    border-right: rgb(0, 0, 0) 1px solid;
    list-style: none;
    height:72vh;
}
.users{
    grid-area: users;
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: center;
    background-color: rgb(26, 25, 25);
    border: 1px solid black;
    height: 10vh;
}
.footer{
    grid-area: footer;
    height: 8vh;
}
h1 {
    font-family: 'Helvetica', 'Arial', sans-serif;
    color: white;
    font-style: bold;
    margin: 1em;
} 

@media only screen and (max-width:700px){
    .Room{
        grid-template-rows: 5vh, 5vh, 65vh, 25vh, 3vh;
        grid-gap: 0.5rem;
        grid-template-areas: "header" "title" "video" "chat" "footer";
    }
    .video{
        grid-area: video;
        height: 50vh;
    }
    .chat{
        margin-top: 0;
        grid-area: chat;
        justify-content: flex-start;
    }
    h1{
        margin: 0;
    }
}
.video-container {
    width: 72vw;
    height: 70vh;
    border: 1px solid black;
}
.footer-container {
    width: 100vw;
    height: 8vh;
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white;
    background: rgb(0, 0, 0);
    bottom: 0px;
}

@media only screen and (max-width:900px) and (min-width:700px) {
    .footer-container {
        width: 100vw;
        padding-left: 1em;
        padding-right: 1em;
        height: 4em;
        align-items: center;
        flex-direction: column;
    }
}

@media only screen and (max-width:700px) {
    .footer-container {
        width: 100vw;
        padding-left: 1em;
        padding-right: 1em;
        height: 4em;
        align-items: center;
        flex-direction: column;
    }
}

.footer-p{
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 5vw;
    height: 1em;
    font-family: 'Helvetica', 'Arial', sans-serif;
}
.messages{
    font-family: 'Helvetica', 'Arial', sans-serif;
    color: white;
    list-style: none;
    max-width: 23vw;
    background-color:rgb(1, 103, 123);
    border-radius: 0.3em;
    padding: 5px;
    margin: 5px 0 0 0;
}
.messageUser::after{
    white-space: pre;
}
.messageTime{
    white-space: nowrap;
    font-family: 'Courier New', Courier, monospace;
    font-size:small;
    margin: 0.5vh;
}
.messageUser{
    white-space: nowrap;
    margin: 0.5vh;
    font-weight: bold;
}
.messageText{
    margin: 5px;
    padding-bottom: 0;
    overflow-wrap: break-word;
}
@media only screen and (max-width:700px){
    .messages{
        margin: 1px;
        margin-right: 1vh;
    }
    .messageUser::after{
        content: normal;
        white-space: pre;
    }

}
.chat-input-label{
