}

.modal-btn {
    background-color: white;
    color: black;
    width: 30px;
    height: 25px;
    right: 10px;
    top: 10px;
    position: fixed;
    font-size: larger;
    font-weight: bold;
}

.modal .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed;
}

.overlay {
    background-color: rgba(40, 40, 40, 0.8);
    z-index: 2;
}

.modal-content {
    z-index: 3;
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: black;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 600px;
    min-width: 300px;
}
#background-video {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}

@media only screen and (max-width:700px){
  #background-video {
    visibility: hidden;
  }
}
.room-element{
    display: grid;
    cursor: pointer;
    color: white;
    border-radius: 0.3em;
    border: 1px black solid;
    height: 5vh;
    padding: 0.5vh 0.5vw 0.5vh 0.5vw;
    margin: 0.5vw;
    overflow-y: hidden;
    background-color:rgb(1, 103, 123);
}

.room-element:hover{
    background-color:rgb(0, 217, 255);
    height: 13vh;
    z-index: 1;
    /* margin-top: -2vh;
    margin-left: -1vw; */
    margin: 0 -2vw -2vh 0;
    transition: 100ms;
}
.room-element:focus{
    background-color:rgb(0, 217, 255);
    height: 13vh;
    z-index: 1; 
}
.room-element-name{
    font-weight: bold;
    height: 6vh;
    margin: 0;
}
.room-element-count{
    color: black;
}
.flex-rooms{
    display: flex;
    height: 37vh;
    flex-direction: column;
    justify-content: flex-start;
    background-color: rgb(26, 25, 25);
