}
.room-list{
    display: grid;
    height: 28vh;
    padding: 0 3vw 0 3vw;
    grid-template-columns: repeat(7, 1fr);
    grid-auto-rows: 8vh;
    overflow-y: auto;
}

@media only screen and (max-width:900px) and (min-width:700px){
    .room-list{
        display: grid;
